.g-actions-panel {
  box-sizing: border-box;
  background-color: var(--g-color-base-brand);
  min-width: 200px;
  height: 52px;
  padding: 4px 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
}
.g-actions-panel__note-wrapper {
  min-width: 100px;
  margin-inline-end: 40px;
}
.g-actions-panel__button-close {
  flex-shrink: 0;
  margin-inline-start: auto;
}