.g-file-preview-actions-desktop {
  position: absolute;
  z-index: 1;
  inset-block-start: -12px;
  inset-inline-end: -12px;
  opacity: 0;
  display: flex;
  gap: var(--g-spacing-1);
}
.g-file-preview-actions-desktop__action-button {
  width: 24px;
  height: 24px;
  justify-content: center;
  align-items: center;
}
.g-file-preview-actions-desktop__action-icon-wrapper {
  display: flex;
}